form
{
    
}

input[type='text'],input[type='date']
{
padding: 1%;
margin: 2%;
border: none;
border-bottom: 1px solid black;
}
form button
{
    background-color:rgb(238, 152, 166);
    padding: 1%;
    margin: 2%;
    border: none;
    transition: all ease .5s;
    width: 10%;
}
button:hover
{
    box-shadow: 2px 2px 2px rgb(222, 45, 104);
}
