.Card {
    margin: 5%;
    padding: 5%;
    background: linear-gradient(white,pink);
    transition: background-color ease-in .5s;
}

.detail
    {
        transition: opacity 0.5s ease-in;
    }
.Card:hover {
    background: linear-gradient(white,pink);
    /* transition: backgrou ease-in 1s; */
}
.Card button
{
    background-color:rgb(238, 152, 166);
    padding: 2%;
    margin: 3%;
    border: none;
    transition: all ease .5s;
    width: 30%;
}
button:hover
{
    box-shadow: 2px 2px 2px rgb(222, 45, 104);
}
